const actions = {
    TRACK_PRODUCT_ISBN: "TRACK_PRODUCT_ISBN",
    TRACK_PRODUCT_PRICE: "TRACK_PRODUCT_PRICE",
    TRACK_PRODUCT_CONDITION: "TRACK_PRODUCT_CONDITION",
    TRACK_PRODUCT_DESCRIPTION: "TRACK_PRODUCT_DESCRIPTION",
    TRACK_PRODUCT_SELLER_NAME: "TRACK_PRODUCT_SELLER_NAME",
    TRACK_PRODUCT_SELLER_RATING: "TRACK_PRODUCT_SELLER_RATING",
    TRACK_PRODUCT_SELLER_RATING_PERCENT: "TRACK_PRODUCT_SELLER_RATING_PERCENT",
    TRACK_PRODUCT_SHIP_US: "TRACK_PRODUCT_SHIP_US"
}

let initialState = {
    isbn: "",
    price: "",
    condition: [],
    description: [],
    seller_name: [],
    seller_rating: "",
    seller_rating_percent: "",
    ships_from_us_only: false,
}

const trackProductReducer = (state=initialState, action) => {
    switch(action.type) {
        case actions.TRACK_PRODUCT_ISBN:
            return {
                ...state,
                isbn: action.payload
            }
        case actions.TRACK_PRODUCT_PRICE:
            return {
                ...state,
                price: action.payload
            }
        case actions.TRACK_PRODUCT_CONDITION:
            return {
                ...state,
                condition: action.payload
            }
        case actions.TRACK_PRODUCT_DESCRIPTION:
            return {
                ...state,
                description: action.payload
            }
        case actions.TRACK_PRODUCT_SELLER_NAME:
            return {
                ...state,
                seller_name: action.payload
            }
        case actions.TRACK_PRODUCT_SELLER_RATING:
            return {
                ...state,
                seller_rating: action.payload
            }
        case actions.TRACK_PRODUCT_SELLER_RATING_PERCENT:
            return {
                ...state,
                seller_rating_percent: action.payload
            }
        case actions.TRACK_PRODUCT_SHIP_US:
            return {
                ...state,
                ships_from_us_only: action.payload
            }
        default:
            return state;
    }
}

export default trackProductReducer;
export {
    actions
}