const actions = {
    STATISTICS_GET: "STATISTICS_GET"
}

let initialState = {
    statistics: null
}

const statisticsReducer = (state=initialState, action) => {
    switch(action.type) {
        case actions.STATISTICS_GET:
            return {
                ...state,
                statistics: action.payload
            }
        default:
            return state;
    }
}

export default statisticsReducer;
export {
    actions
}