import React, {Component} from 'react';
import PriceHistoryChart from './PriceHistoryChart';
import OffersHistoryChart from './OffersHistoryChart';
import {connect} from 'react-redux';
import {actions} from '../../../actions';
import { Col, Row, Container } from 'react-bootstrap';
import {List, ListItem, Checkbox, InputLabel} from '@material-ui/core';
import './assets/css/style.css';

import price_history_day_range from '../../../json_data/price_history_ranges/price_history_day_range.json';
import price_history_week_range from '../../../json_data/price_history_ranges/price_history_week_range.json';
import price_history_month_range from '../../../json_data/price_history_ranges/price_history_month_range.json';
import price_history_month_3_range from '../../../json_data/price_history_ranges/price_history_month_3_range.json';
import price_history_year_range from '../../../json_data/price_history_ranges/price_history_year_range.json';

import offers_history_day_range from '../../../json_data/offers_history_ranges/offers_history_day_range.json';
import offers_history_week_range from '../../../json_data/offers_history_ranges/offers_history_week_range.json';
import offers_history_month_range from '../../../json_data/offers_history_ranges/offers_history_month_range.json';
import offers_history_month_3_range from '../../../json_data/offers_history_ranges/offers_history_month_3_range.json';
import offers_history_year_range from '../../../json_data/offers_history_ranges/offers_history_year_range.json';

class PriceHistory extends Component {
    constructor () {
        super()

        this.onClickVisibility = this.onClickVisibility.bind(this)
        this.changeRange = this.changeRange.bind(this)
        this.rangeChanged = this.rangeChanged.bind(this)
    }

    rangeChanged(e) {
        console.log("type : " + e.type + ", trigger : " + e.trigger + ", AxisX viewportMininum : " + e.axisX[0].viewportMinimum + ", AxisX viewportMaximum : " + e.axisX[0].viewportMaximum);
    }

    changeRange(e) {
        let inp = e.target;

        this.props.change_period(inp.id);
        this.getPriceHistoryRanges(inp.id);
        this.getOffersHistoryRanges(inp.id);
    }

    onClickVisibility (e) {
        let inp = e.target;
        this.props.show_price(inp.id);
    }

    getPriceHistoryRanges (range) {
        let price_history_range_data = null;
        switch(range || this.props.price_history.range) {
            case actions.PRICE_HISTORY_RANGE_DAY:
                price_history_range_data = price_history_day_range;
                break
            case actions.PRICE_HISTORY_RANGE_WEEK:
                price_history_range_data = price_history_week_range;
                break;
            case actions.PRICE_HISTORY_RANGE_MONTH:
                price_history_range_data = price_history_month_range;
                break;
            case actions.PRICE_HISTORY_RANGE_3_MONTH:
                price_history_range_data = price_history_month_3_range;
                break;
            case actions.PRICE_HISTORY_RANGE_YEAR:
                price_history_range_data = price_history_year_range;
                break;
        }

        if (price_history_range_data) {
            this.props.set_amazon_range(price_history_range_data.amazon);
            this.props.set_new_range(price_history_range_data.newone);
            this.props.set_used_like_new_range(price_history_range_data.used_like_new);
            this.props.set_used_very_good_range(price_history_range_data.used_very_good);
            this.props.set_used_good_range(price_history_range_data.used_good);
            this.props.set_used_acceptable_range(price_history_range_data.used_acceptable);
            this.props.set_sales_rank_range(price_history_range_data.salesRank);
        }
    }

    getOffersHistoryRanges (range) {
        let offers_history_range_data = null;
        switch(range || this.props.price_history.range) {
            case actions.PRICE_HISTORY_RANGE_DAY:
                offers_history_range_data = offers_history_day_range;
                break
            case actions.PRICE_HISTORY_RANGE_WEEK:
                offers_history_range_data = offers_history_week_range;
                break;
            case actions.PRICE_HISTORY_RANGE_MONTH:
                offers_history_range_data = offers_history_month_range;
                break;
            case actions.PRICE_HISTORY_RANGE_3_MONTH:
                offers_history_range_data = offers_history_month_3_range;
                break;
            case actions.PRICE_HISTORY_RANGE_YEAR:
                offers_history_range_data = offers_history_year_range;
                break;
        }

        if (offers_history_range_data) {
            this.props.set_used_offers(offers_history_range_data.used_offers);
            this.props.set_new_offers(offers_history_range_data.new_offers);
        }
    }

    componentWillMount () {
        this.getPriceHistoryRanges();
        this.getOffersHistoryRanges();
    }
    render () {
        
        const PH = this.props.price_history;
        
        return (
            <Container id="price-history" fluid={true}>
                <Row>
                    <Col xs={10}>
                        <PriceHistoryChart rangeChanged={this.rangeChanged} />
                        {this.props.offers_history.offers_history_visible ? <OffersHistoryChart rangeChanged={this.rangeChanged} /> : ""}
                    </Col>
                    <Col xs={2}>
                        <List>
                            <ListItem button={true} className="price-history-li price-history-li-amazon">
                                <Checkbox id={actions.PRICE_HISTORY_AMAZON_VISIBLE} onChange={this.onClickVisibility} checked={PH.amazon_visible} className="price-history-li-amazon-cb" /> 
                                <InputLabel className="price-history-li-amazon-label" htmlFor={actions.PRICE_HISTORY_AMAZON_VISIBLE}>Amazon</InputLabel>
                            </ListItem>
                            <ListItem button={true} className="price-history-li price-history-li-new">
                                <Checkbox id={actions.PRICE_HISTORY_NEW_VISIBLE} onChange={this.onClickVisibility} checked={PH.new_visible} className="price-history-li price-history-li-new-cb" /> 
                                <InputLabel className="price-history-li-new-label" htmlFor={actions.PRICE_HISTORY_NEW_VISIBLE}>New</InputLabel>
                            </ListItem>
                            <ListItem button={true} className="price-history-li price-history-li-used-all">
                                <Checkbox className="price-history-li-used-all-cb" id={actions.PRICE_HISTORY_USED_ALL_VISIBLE} onChange={this.onClickVisibility} checked={PH.used_all_visible} /> 
                                <InputLabel htmlFor={actions.PRICE_HISTORY_USED_ALL_VISIBLE} className="price-history-li-used-all-label">Used</InputLabel>
                            </ListItem>
                            <ListItem button={true} className="price-history-li price-history-li-used-like-new">
                                <Checkbox className="price-history-li-used-like-new-cb" id={actions.PRICE_HISTORY_USED_LIKE_NEW_VISIBLE} onChange={this.onClickVisibility} checked={PH.used_like_new_visible} /> 
                                <InputLabel htmlFor={actions.PRICE_HISTORY_USED_LIKE_NEW_VISIBLE} className="price-history-li-used-like-new-label">Used - Like New</InputLabel>
                            </ListItem>
                            <ListItem button={true} className="price-history-li price-history-li-used-very-good">
                                <Checkbox className="price-history-li-used-very-good-cb" id={actions.PRICE_HISTORY_USED_VERY_GOOD_VISIBLE} onChange={this.onClickVisibility} checked={PH.used_very_good_visible} /> 
                                <InputLabel htmlFor={actions.PRICE_HISTORY_USED_VERY_GOOD_VISIBLE} className="price-history-li-used-very-good-label">Used - Very Good</InputLabel>
                            </ListItem>
                            <ListItem button={true} className="price-history-li price-history-li-used-good">
                                <Checkbox className="price-history-li-used-good-cb" id={actions.PRICE_HISTORY_USED_GOOD_VISIBLE} onChange={this.onClickVisibility} checked={PH.used_good_visible} /> 
                                <InputLabel htmlFor={actions.PRICE_HISTORY_USED_GOOD_VISIBLE} className="price-history-li-used-good-label">Used - Good</InputLabel>
                            </ListItem>
                            <ListItem button={true} className="price-history-li price-history-li-used-acceptable">
                                <Checkbox className="price-history-li-used-acceptable-cb" id={actions.PRICE_HISTORY_USED_ACCEPTABLE_VISIBLE} onChange={this.onClickVisibility} checked={PH.used_acceptable_visible} /> 
                                <InputLabel htmlFor={actions.PRICE_HISTORY_USED_ACCEPTABLE_VISIBLE} className="price-history-li-used-acceptable-label">Used - Acceptable</InputLabel>
                            </ListItem>
                            <ListItem button={true} className="price-history-li price-history-li-sales-rank">
                                <Checkbox className="price-history-li-sales-rank-cb" id={actions.PRICE_HISTORY_SALES_RANK_VISIBLE} onChange={this.onClickVisibility} checked={PH.salesRank_visible} /> 
                                <InputLabel className="price-history-li-sales-rank-label" htmlFor={actions.PRICE_HISTORY_SALES_RANK_VISIBLE}>Sales Rank</InputLabel>
                            </ListItem>
                            
                            <ListItem onClick={this.changeRange} button={true} style={{color:"black",fontSize:'0.7em',padding:"0px 5px",fontWeight: PH.range===actions.PRICE_HISTORY_RANGE_DAY ? "bolder" : "normal"}} id={actions.PRICE_HISTORY_RANGE_DAY}>Day</ListItem>
                            <ListItem onClick={this.changeRange} button={true} style={{color:"black",fontSize:'0.7em',padding:"0px 5px",fontWeight: PH.range===actions.PRICE_HISTORY_RANGE_WEEK ? "bolder" : "normal"}} id={actions.PRICE_HISTORY_RANGE_WEEK}>Week</ListItem>
                            <ListItem onClick={this.changeRange} button={true} style={{color:"black",fontSize:'0.7em',padding:"0px 5px",fontWeight: PH.range===actions.PRICE_HISTORY_RANGE_MONTH ? "bolder" : "normal"}} id={actions.PRICE_HISTORY_RANGE_MONTH}>Month</ListItem>
                            <ListItem onClick={this.changeRange} button={true} style={{color:"black",fontSize:'0.7em',padding:"0px 5px",fontWeight: PH.range===actions.PRICE_HISTORY_RANGE_3_MONTH ? "bolder" : "normal"}} id={actions.PRICE_HISTORY_RANGE_3_MONTH}>3 Month</ListItem>
                            <ListItem onClick={this.changeRange} button={true} style={{color:"black",fontSize:'0.7em',padding:"0px 5px",fontWeight: PH.range===actions.PRICE_HISTORY_RANGE_YEAR ? "bolder" : "normal"}} id={actions.PRICE_HISTORY_RANGE_YEAR}>Year</ListItem>
                        </List>
                    </Col>
                </Row>
            </Container>
        )
    }
}

const mapStateToProps = state => ({
    price_history: state.price_history,
    offers_history: state.offers_history,
})

const mapDispatchToProps = dispatch => {
    return {
        show_price: action => dispatch({type: action}),
        set_amazon_range: payload => dispatch({type: actions.PRICE_HISTORY_AMAZON_RANGE, payload}),
        set_new_range: payload => dispatch({type: actions.PRICE_HISTORY_NEW_RANGE, payload}),
        set_used_like_new_range: payload => dispatch({type: actions.PRICE_HISTORY_USED_LIKE_NEW_RANGE, payload}),
        set_used_very_good_range: payload => dispatch({type: actions.PRICE_HISTORY_USED_VERY_GOOD_RANGE, payload}),
        set_used_good_range: payload => dispatch({type: actions.PRICE_HISTORY_USED_GOOD_RANGE, payload}),
        set_used_acceptable_range: payload => dispatch({type: actions.PRICE_HISTORY_USED_ACCEPTABLE_RANGE, payload}),
        set_sales_rank_range: payload => dispatch({type: actions.PRICE_HISTORY_SALES_RANK_RANGE, payload}),
        set_used_offers: payload => dispatch({type: actions.OFFERS_HISTORY_USED_RANGE, payload}),
        set_new_offers: payload => dispatch({type: actions.OFFERS_HISTORY_NEW_RANGE, payload}),
        change_period: action => dispatch({type: action}),
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(PriceHistory);