import React, {Component} from 'react';
import {Icon} from '@material-ui/core';
import {actions} from '../../actions';
import {connect} from 'react-redux';
import './style.css';

class MoreHistoricalData extends Component {
    constructor () {
        super();
    }

    render () {
        return <div className="more-historical-data-container">
            <a href="" onClick={e => {
                e.preventDefault();
                this.props.set_offers_history_visibility();
            }} className="more-historical-data-link">{this.props.offers_history_visible ? <Icon>minimize</Icon> : <Icon>history</Icon>}
            {this.props.offers_history_visible ? "Less Historical Data" : "More Historical Data"}</a>
        </div>
    }
}

const mapStateToProps = state => {
    return {
        offers_history: state.offers_history
    }
}

const mapDispatchToProps = dispatch => {
    return {
        set_offers_history_visibility: () => dispatch({type: actions.OFFERS_HISTORY_VISIBLE})
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(MoreHistoricalData);