import React, {Component} from 'react';
import statistics from './statistics.json';
import EqualizerIcon from '@material-ui/icons/Equalizer';
import {Container, Col, Row, Table} from 'react-bootstrap';
import './style.css';

class Statistics extends Component {
    constructor () {
        super();

        this.state = {
            visible: false
        }
    }
    render () {
        return <div className="statistics-popup-container">
            <div className="statistics-popup-inner" style={{display: (this.state.visible ? 'inline-block': 'none')}}>{this.getStatistics()}</div>
            <a href="" onMouseOver={() => {
                this.setState({
                    ...this.state,
                    visible: true
                })
            }} onMouseOut={() => {
                this.setState({
                    ...this.state,
                    visible: false
                })
            }} onClick={(e) => e.preventDefault()} className="statistics-link"><EqualizerIcon /> Statistics</a>
        </div>
    }

    getStatistics () {
        return (
            <Table striped hover>
                <colgroup>
                    <col />
                    <col style={{borderLeft: "2px solid orange", background: "rgba(255, 255, 0, 0.1)"}} />
                    <col style={{borderLeft: "2px solid blue", background: "rgba(0, 0, 255, 0.1)"}} />
                    <col style={{borderLeft: "2px solid black", background: "rgba(0, 0, 0, 0.1)"}} />
                    <col style={{borderLeft: "2px solid black", background: "rgba(0, 255, 0, 0.1)"}} />
                </colgroup>
                <thead>
                    <tr>
                        <th></th>
                        <th>Amazon</th>
                        <th>New</th>
                        <th>Used</th>
                        <th>Sales Rank</th>
                    </tr>
                </thead>
                <tbody>
                    <tr className="lowest-text">
                        <td>Lowest</td>
                        <td>{statistics.amazon.lowest.map((value, index) => {
                            return (
                                <div key={`amazon_lowest${index}`}>
                                    <strong>${value.price}</strong>
                                    <p style={{color:'#aaa', fontSize: '0.8em'}}>{value.when}</p>
                                </div>
                            )
                        })}</td>
                        <td>{statistics.new.lowest.map((value, index) => {
                            return (
                                <div key={`new_lowest${index}`}>
                                    <strong>${value.price}</strong>
                                    <p style={{color:'#aaa', fontSize: '0.8em'}}>{value.when}</p>
                                </div>
                            )
                        })}</td>
                        <td>{statistics.used.lowest.map((value, index) => {
                            return (
                                <div key={`used_lowes${index}`}>
                                    <strong>${value.price}</strong>
                                    <p style={{color:'#aaa', fontSize: '0.8em'}}>{value.when}</p>
                                </div>
                            )
                        })}</td>
                        <td>{statistics.sales_rank.lowest.map((value, index) => {
                            return (
                                <div key={`sales_rank_lowest${index}`}>
                                    <strong>${value.price}</strong>
                                    <p style={{color:'#aaa', fontSize: '0.8em'}}>{value.when}</p>
                                </div>
                            )
                        })}</td>
                    </tr>
                    <tr className="current-text">
                        <td>Current</td>
                        <td>{statistics.amazon.current.map((value, index) => {
                            return (
                                <div key={`amazon_current${index}`}>
                                    <strong>${value.price}</strong>
                                    <p style={{color:'#aaa', fontSize: '0.8em'}}>{value.when}</p>
                                </div>
                            )
                        })}</td>
                        <td>{statistics.new.current.map((value, index) => {
                            return (
                                <div key={`new_current${index}`}>
                                    <strong>${value.price}</strong>
                                    <p style={{color:'#aaa', fontSize: '0.8em'}}>{value.when}</p>
                                </div>
                            )
                        })}</td>
                        <td>{statistics.used.current.map((value, index) => {
                            return (
                                <div key={`used_current${index}`}>
                                    <strong>${value.price}</strong>
                                    <p style={{color:'#aaa', fontSize: '0.8em'}}>{value.when}</p>
                                </div>
                            )
                        })}</td>
                        <td>{statistics.sales_rank.current.map((value, index) => {
                            return (
                                <div key={`sales_rank_current${index}`}>
                                    <strong>${value.price}</strong>
                                    <p style={{color:'#aaa', fontSize: '0.8em'}}>{value.when}</p>
                                </div>
                            )
                        })}</td>
                    </tr>
                    <tr className="highest-text">
                        <td>Highest</td>
                        <td>{statistics.amazon.highest.map((value, index) => {
                            return (
                                <div key={`amazon_highest${index}`}>
                                    <strong>${value.price}</strong>
                                    <p style={{color:'#aaa', fontSize: '0.8em'}}>{value.when}</p>
                                </div>
                            )
                        })}</td>
                        <td>{statistics.new.highest.map((value, index) => {
                            return (
                                <div key={`new_highest${index}`}>
                                    <strong>${value.price}</strong>
                                    <p style={{color:'#aaa', fontSize: '0.8em'}}>{value.when}</p>
                                </div>
                            )
                        })}</td>
                        <td>{statistics.used.highest.map((value, index) => {
                            return (
                                <div key={`used_highest${index}`}>
                                    <strong>${value.price}</strong>
                                    <p style={{color:'#aaa', fontSize: '0.8em'}}>{value.when}</p>
                                </div>
                            )
                        })}</td>
                        <td>{statistics.sales_rank.highest.map((value, index) => {
                            return (
                                <div key={`sales_rank_highest${index}`}>
                                    <strong>${value.price}</strong>
                                    <p style={{color:'#aaa', fontSize: '0.8em'}}>{value.when}</p>
                                </div>
                            )
                        })}</td>
                    </tr>
                    <tr className="average-text">
                        <td>Average</td>
                        <td>{statistics.amazon.average.map((value, index) => {
                            return (
                                <div key={`amazon_average${index}`}>
                                    <strong>${value.price}</strong>
                                    <p style={{color:'#aaa', fontSize: '0.8em'}}>{value.when}</p>
                                </div>
                            )
                        })}</td>
                        <td>{statistics.new.average.map((value, index) => {
                            return (
                                <div key={`new_average${index}`}>
                                    <strong>${value.price}</strong>
                                    <p style={{color:'#aaa', fontSize: '0.8em'}}>{value.when}</p>
                                </div>
                            )
                        })}</td>
                        <td>{statistics.used.average.map((value, index) => {
                            return (
                                <div key={`used_average${index}`}>
                                    <strong>${value.price}</strong>
                                    <p style={{color:'#aaa', fontSize: '0.8em'}}>{value.when}</p>
                                </div>
                            )
                        })}</td>
                        <td>{statistics.sales_rank.average.map((value, index) => {
                            return (
                                <div key={`sales_rank_average${index}`}>
                                    <strong>${value.price}</strong>
                                    <p style={{color:'#aaa', fontSize: '0.8em'}}>{value.when}</p>
                                </div>
                            )
                        })}</td>
                    </tr>
                    <tr className="drops-text">
                        <td>Drops</td>
                        <td>{statistics.amazon.drops.map((value, index) => {
                            return (
                                <div key={`amazon_drops${index}`}>
                                    <strong>${value.price}</strong>
                                    <p style={{color:'#aaa', fontSize: '0.8em'}}>{value.when}</p>
                                </div>
                            )
                        })}</td>
                        <td>{statistics.new.drops.map((value, index) => {
                            return (
                                <div key={`new_drops${index}`}>
                                    <strong>${value.price}</strong>
                                    <p style={{color:'#aaa', fontSize: '0.8em'}}>{value.when}</p>
                                </div>
                            )
                        })}</td>
                        <td>{statistics.used.drops.map((value, index) => {
                            return (
                                <div key={`used_drops${index}`}>
                                    <strong>${value.price}</strong>
                                    <p style={{color:'#aaa', fontSize: '0.8em'}}>{value.when}</p>
                                </div>
                            )
                        })}</td>
                        <td>{statistics.sales_rank.drops.map((value, index) => {
                            return (
                                <div key={`sales_rank${index}`}>
                                    <strong>${value.price}</strong>
                                    <p style={{color:'#aaa', fontSize: '0.8em'}}>{value.when}</p>
                                </div>
                            )
                        })}</td>
                    </tr>
                </tbody>
            </Table>
        );
    }
}

export default Statistics;