import React, {Component} from 'react';
import {Container, Col, Row} from 'react-bootstrap';
import {TextField, Checkbox, FormControlLabel, Button} from '@material-ui/core';
import ChipInput from 'material-ui-chip-input';
import {connect} from 'react-redux';
import './css/style.css';
import { actions } from '../../../actions';

class TrackProduct extends Component {
    constructor (props) {
        super(props)

        this.inputChanged = this.inputChanged.bind(this)
        this.conditionChecked = this.conditionChecked.bind(this)
        this.shippingChecked = this.shippingChecked.bind(this)
        this.saveAlert = this.saveAlert.bind(this)
    }

    setGlobalSettings () {
        // if (!this.props.track_product.price && this.props.settings.price) {
        //     this.props.set_price(this.props.settings.price)
        // }
        
        if (!this.props.track_product.seller_rating && this.props.settings.seller_rating) {
            this.props.set_seller_rating(this.props.settings.seller_rating)
        }
        
        if (!this.props.track_product.seller_rating_percent && this.props.settings.seller_rating_percent) {
            this.props.set_seller_rating_percent(this.props.settings.seller_rating_percent)
        }
        
        if (!this.props.track_product.condition.length && this.props.settings.condition.length) {
            this.props.set_conditions(this.props.settings.condition.slice(0))
        }
        
        if (!this.props.track_product.description.length && this.props.settings.description.length) {
            this.props.set_description(this.props.settings.description.slice(0))
        }
        
        if (!this.props.track_product.seller_name.length && this.props.settings.seller_name.length) {
            this.props.set_seller_name(this.props.settings.seller_name.slice(0))
        }
        
        if (!this.props.track_product.ships_from_us_only && this.props.settings.ships_from_us_only) {
            this.props.set_shipping(this.props.settings.ships_from_us_only)
        }
    }

    setISBN () {
        if (!this.props.track_product.isbn)
            this.props.set_isbn(window.location.hash.replace("#", ""));
    }

    componentDidMount () {
        this.setISBN();
        this.setGlobalSettings();
    }

    saveAlert (e) {
        console.log(this.props.track_product);
    }

    shippingChecked (e) {
        const target = e.target;
        const track_product = this.props.track_product;
        this.props.set_shipping(!track_product.ships_from_us_only)
    }

    conditionChecked (e) {
        const target = e.target;
        const value = target.value;
        const track_product = this.props.track_product
        const index = track_product.condition.indexOf(value);
        if (index === -1) {
            track_product.condition.push(value);
        } else {
            track_product.condition.splice(index, 1);
        }

        this.props.set_conditions(track_product.condition);
    }

    inputChanged (e) {
        const target = e.target;
        switch(target.id) {
            case actions.TRACK_PRODUCT_ISBN:
                this.props.set_isbn(target.value);
                break;
            case actions.TRACK_PRODUCT_PRICE:
                this.props.set_price(target.value);
                break;
            case actions.TRACK_PRODUCT_SELLER_RATING:
                this.props.set_seller_rating(target.value);
                break;
            case actions.TRACK_PRODUCT_SELLER_RATING_PERCENT:
                this.props.set_seller_rating_percent(target.value);
                break;
        }
    }
    render () {
        const track_product = this.props.track_product;

        return (
            <Container id="track-product" fluid={true}>
                <Row>
                    <Col lg={3} md={3} xs={4}>
                        <TextField
                            id={actions.TRACK_PRODUCT_ISBN}
                            value={this.props.track_product.isbn}
                            label="ISBN"
                            margin="normal"
                            variant="standard"
                            onChange={this.inputChanged}
                        />
                        <TextField
                            id={actions.TRACK_PRODUCT_PRICE}
                            value={this.props.track_product.price}
                            label="Price (Less than)"
                            margin="normal"
                            variant="standard"
                            type="number"
                            onChange={this.inputChanged}
                        />
                        <TextField
                            id={actions.TRACK_PRODUCT_SELLER_RATING}
                            value={this.props.track_product.seller_rating}
                            label="Seller rating - Min. # Feedback"
                            margin="normal"
                            variant="standard"
                            type="number"
                            onChange={this.inputChanged}
                        />
                        <TextField
                            id={actions.TRACK_PRODUCT_SELLER_RATING_PERCENT}
                            value={this.props.track_product.seller_rating_percent}
                            label="Seller Rating - Min. %"
                            margin="normal"
                            variant="standard"
                            type="number"
                            onChange={this.inputChanged}
                        />
                    </Col>
                    <Col lg={6} md={5} xs={4}>
                        <div className="text-center">Condition:</div>
                        <Row>
                            <Col lg={4} md={6} xs={12}>
                                <FormControlLabel
                                    control={
                                    <Checkbox
                                        checked={track_product.condition.indexOf("new") !== -1}
                                        onChange={this.conditionChecked}
                                        value="new"
                                    />
                                    }
                                    label="New"
                                />
                                <FormControlLabel
                                    control={
                                    <Checkbox
                                        checked={track_product.condition.indexOf("used_like_new") !== -1}
                                        onChange={this.conditionChecked}
                                        value="used_like_new"
                                    />
                                    }
                                    label="Used - Like New"
                                />
                            </Col>
                            <Col lg={4} md={6} xs={12}>
                                <FormControlLabel
                                    control={
                                    <Checkbox
                                        checked={track_product.condition.indexOf("used_very_good") !== -1}
                                        onChange={this.conditionChecked}
                                        value="used_very_good"
                                    />
                                    }
                                    label="Used - Very Good"
                                />
                                <FormControlLabel
                                    className="form-control-label"
                                    control={
                                    <Checkbox
                                        checked={track_product.condition.indexOf("used_good") !== -1}
                                        onChange={this.conditionChecked}
                                        value="used_good"
                                    />
                                    }
                                    label="Used - Good"
                                />
                            </Col>
                            <Col lg={4} md={6} xs={12}>
                                <FormControlLabel
                                    className="form-control-label"
                                    control={
                                    <Checkbox
                                        checked={track_product.condition.indexOf("used_acceptable") !== -1}
                                        onChange={this.conditionChecked}
                                        value="used_acceptable"
                                    />
                                    }
                                    label="Used - Acceptable"
                                />
                            </Col>
                        </Row>
                    </Col>
                    <Col xs={4} lg={3}>
                        <FormControlLabel
                            control={
                                <ChipInput
                                    // style={{maxHeight: "90px", overflow: "auto"}}
                                    value={this.props.track_product.description}
                                    onAdd={chip => {
                                        const chips = this.props.track_product.description;
                                        chips.push(chip);
                                        this.props.set_description(chips);
                                    }}
                                    onDelete={(chip, index) => {
                                        const chips = this.props.track_product.description;
                                        chips.splice(chips.indexOf(chip), 1);
                                        this.props.set_description(chips);
                                    }}
                            />
                            }
                            label="Description (Does NOT Contain)"
                        />
                        <FormControlLabel
                            control={
                                <ChipInput
                                    // style={{maxHeight: "90px", overflow: "auto"}}
                                    value={this.props.track_product.seller_name}
                                    onAdd={chip => {
                                        const chips = this.props.track_product.seller_name;
                                        chips.push(chip);
                                        this.props.set_seller_name(chips);
                                    }}
                                    onDelete={(chip, index) => {
                                        const chips = this.props.track_product.seller_name;
                                        chips.splice(chips.indexOf(chip), 1);
                                        this.props.set_seller_name(chips);
                                    }}
                            />
                            }
                            label="Seller name (Is NOT)"
                        />
                        <FormControlLabel
                            control={
                                <Checkbox
                                    checked={track_product.ships_from_us_only}
                                    onChange={this.shippingChecked}
                                />
                            }
                            label="Shipping Location - Ships from US only (if known)"
                        />
                    </Col>
                </Row>
                <Row>
                    <Col xs={12} className="text-center">
                        <Button color="primary" variant="contained" onClick={this.saveAlert}>Add Alert</Button>
                    </Col>
                </Row>
            </Container>
        )
    }
}

const mapStateToProps = state => {
    return {
        track_product: state.track_product,
        settings: state.settings
    }
}

const mapDispatchToProps = dispatch => {
    return {
        set_isbn: payload => dispatch({type: actions.TRACK_PRODUCT_ISBN, payload}),
        set_price: payload => dispatch({type: actions.TRACK_PRODUCT_PRICE, payload}),
        set_conditions: payload => dispatch({type: actions.TRACK_PRODUCT_CONDITION, payload}),
        set_description: payload => dispatch({type: actions.TRACK_PRODUCT_DESCRIPTION, payload}),
        set_seller_name: payload => dispatch({type: actions.TRACK_PRODUCT_SELLER_NAME, payload}),
        set_seller_rating: payload => dispatch({type: actions.TRACK_PRODUCT_SELLER_RATING, payload}),
        set_seller_rating_percent: payload => dispatch({type: actions.TRACK_PRODUCT_SELLER_RATING_PERCENT, payload}),
        set_shipping: payload => dispatch({type: actions.TRACK_PRODUCT_SHIP_US, payload})
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(TrackProduct);