import React, {Component} from 'react';
import {connect} from 'react-redux';
import {actions} from './actions';
import {AppBar, Tabs, Tab, CircularProgress} from '@material-ui/core';
import Statistics from './components/statistics';
import MoreHistoricalData from './components/more_historical_data';
import Loading from './components/loading';
import 'bootstrap/dist/css/bootstrap.min.css';
import {Container, Col, Row} from 'react-bootstrap';

const tabStyle = {
  fontSize: ".8em",
  minHeight: 'auto',
  minWidth: '50px',

}

const tabsStyle = {
  minHeight: 'auto'
}

class App extends Component {
  constructor () {
    super();

    this.state = {
      activeIndex: actions.CONTENT_PRICE_HISTORY,
      loading: true
    }

    this.changeTab = this.changeTab.bind(this)
  }

  componentDidMount () {
    this.props[this.state.activeIndex]();

    setTimeout(() => {
      this.setState({
        ...this.state,
        loading: false,
      })
    }, 100);
  }

  changeTab (e, index) {
    this.setState({
      ...this.state,
      activeIndex: index
    });

    this.props[index]();
  }
  render () {
    if (this.state.loading) {
      return <Loading />
    }

    return (
      <div>
        <AppBar position="static" color="default">
          <Tabs value={this.state.activeIndex} onChange={this.changeTab} style={tabsStyle}>
            <Tab value={actions.CONTENT_PRICE_HISTORY} style={tabStyle} label="Price history" />
            <Tab value={actions.CONTENT_TRACK_PRODUCT} style={tabStyle} label="Track product" />
            {/* <Tab value={actions.CONTENT_DATA} style={tabStyle} label="Data" /> */}
            <Tab value={actions.CONTENT_SETTINGS} style={tabStyle} label="Settings" />
          </Tabs>
        </AppBar>
        <div style={{minHeight: this.state.loading ? 80 : 450}}>{this.props.content}</div>
          <Container>
            <Row>
              <Col className="text-right"><Statistics /></Col>
              <Col xs={2} className="text-right"><MoreHistoricalData /></Col>
              <Col xs={2}></Col>
            </Row>
          </Container>
      </div>
    )
  }
}

const mapStateToProps = state => ({
  content: state.content,
})

const mapDispatchToProps = dispatch => {
  let obj = {}
  obj[actions.CONTENT_PRICE_HISTORY] = () => dispatch({type: actions.CONTENT_PRICE_HISTORY});
  obj[actions.CONTENT_TRACK_PRODUCT] = () => dispatch({type: actions.CONTENT_TRACK_PRODUCT});
  obj[actions.CONTENT_DATA] = () => dispatch({type: actions.CONTENT_DATA});
  obj[actions.CONTENT_SETTINGS] = () => dispatch({type: actions.CONTENT_SETTINGS});

  return obj
}

export default connect(mapStateToProps, mapDispatchToProps)(App);
