import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import * as serviceWorker from './serviceWorker';
import {createStore, applyMiddleware} from 'redux';
import allReducers from './actions';
import {Provider} from 'react-redux';

// store
let store = createStore(
    allReducers,
    applyMiddleware(state => next => action => {
        console.log("State before dispatch", state.getState().track_product, state.getState().settings);
        console.log("Will dispatch", action);

        const returnTheValue = next(action);

        console.log("State after dispatch", state.getState().track_product, state.getState().settings);

        return returnTheValue;
    }),
    // window.__REDUX_DEVTOOLS_EXTENSION__ && window.__REDUX_DEVTOOLS_EXTENSION__()
);

ReactDOM.render(
    <Provider store={store}>
        <App />
    </Provider>
, document.getElementById('root'));

let rootHeight = document.getElementById('root').offsetHeight;
const sendHeight = () => {
    if (document.getElementById('root') && window.parent && window.parent.postMessage) {
        setTimeout(() => {
            const height = document.getElementById('root').offsetHeight;
            window.parent.postMessage({height}, "*");
        }, 10);
    }
}

setInterval(() => {
    if (rootHeight != document.getElementById('root').offsetHeight) {
        sendHeight();
        rootHeight = document.getElementById('root').offsetHeight;
    }
}, 100);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
