import React, {Component} from 'react';
import {connect} from 'react-redux';
import {actions} from '../../../actions';
import {CanvasJSChart} from '../../canvasjs/canvasjs.react';
import data from './settings_price_history_chart.json';

class PriceHistoryChart extends Component {
    constructor () {
        super()
        this.rangeChanged = this.rangeChanged.bind(this)
    }

    rangeChanged(e) {
        // callback came from property
        this.props.rangeChanged(e);
    }

    render () {
        
        const PH = this.props.price_history;

        data.data[0].dataPoints = PH.amazon;
        data.data[0].visible = PH.amazon_visible;

        data.data[1].dataPoints = PH.newone;
        data.data[1].visible = PH.new_visible;

        data.data[2].dataPoints = PH.used_like_new;
        data.data[2].visible = PH.used_like_new_visible;

        data.data[3].dataPoints = PH.used_very_good;
        data.data[3].visible = PH.used_very_good_visible;

        data.data[4].dataPoints = PH.used_good;
        data.data[4].visible = PH.used_good_visible;

        data.data[5].dataPoints = PH.used_acceptable;
        data.data[5].visible = PH.used_acceptable_visible;

        data.data[6].dataPoints = PH.salesRank;
        data.data[6].visible = PH.salesRank_visible;

        switch(PH.range) {
            case actions.PRICE_HISTORY_RANGE_DAY:
                data.axisX.interval = 10;
                break;
            case actions.PRICE_HISTORY_RANGE_WEEK:
                data.axisX.interval = 50;
                break;
            case actions.PRICE_HISTORY_RANGE_MONTH:
                data.axisX.interval = 50;
                break;
            case actions.PRICE_HISTORY_RANGE_3_MONTH:
                data.axisX.interval = 15;
                break;
            case actions.PRICE_HISTORY_RANGE_YEAR:
                data.axisX.interval = 30;
                break;
        }


        data.rangeChanged = this.rangeChanged;
        data.height = 450;
        
        return (<CanvasJSChart options={data} />);
    }
}

const mapStateToProps = state => ({
    price_history: state.price_history,
})

const mapDispatchToProps = dispatch => {
    return {
        
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(PriceHistoryChart);