import React, {Component} from 'react';
import {Container, Col, Row} from 'react-bootstrap';
import {TextField, Checkbox, FormControlLabel, Button} from '@material-ui/core';
import ChipInput from 'material-ui-chip-input';
import {connect} from 'react-redux';
import './css/style.css';
import { actions } from '../../../actions';

class Settings extends Component {
    constructor () {
        super()

        this.inputChanged = this.inputChanged.bind(this)
        this.conditionChecked = this.conditionChecked.bind(this)
        this.shippingChecked = this.shippingChecked.bind(this)
        this.saveSettings = this.saveSettings.bind(this)
    }

    componentDidMount () {
        
    }

    saveSettings (e) {
        console.log(this.props.settings);
    }

    shippingChecked (e) {
        const settings = this.props.settings;
        this.props.set_shipping(!settings.ships_from_us_only)
    }

    conditionChecked (e) {
        const target = e.target;
        const value = target.value;
        const settings = this.props.settings
        const index = settings.condition.indexOf(value);
        if (index === -1) {
            settings.condition.push(value);
        } else {
            settings.condition.splice(index, 1);
        }

        this.props.set_conditions(settings.condition);
    }

    inputChanged (e) {
        const target = e.target;
        switch(target.id) {
            case actions.SETTINGS_ISBN:
                this.props.set_isbn(target.value);
                break;
            case actions.SETTINGS_PRICE:
                this.props.set_price(target.value);
                break;
            case actions.SETTINGS_SELLER_RATING:
                this.props.set_seller_rating(target.value);
                break;
            case actions.SETTINGS_SELLER_RATING_PERCENT:
                this.props.set_seller_rating_percent(target.value);
                break;
            case actions.SETTINGS_EMAIL:
                this.props.set_email(target.value);
                break;
        }
    }
    render () {
        const settings = this.props.settings;

        return (
            <Container id="track-product" fluid={true}>
                <Row>
                    <Col lg={3} md={3} xs={4}>
                        <TextField
                            id={actions.SETTINGS_SELLER_RATING}
                            value={this.props.settings.seller_rating}
                            label="Seller rating - Min. # Feedback"
                            margin="normal"
                            variant="standard"
                            type="number"
                            onChange={this.inputChanged}
                        />
                        <TextField
                            id={actions.SETTINGS_SELLER_RATING_PERCENT}
                            value={this.props.settings.seller_rating_percent}
                            label="Seller Rating - Min. %"
                            margin="normal"
                            variant="standard"
                            type="number"
                            onChange={this.inputChanged}
                        />
                    </Col>
                    <Col lg={6} md={5} xs={4}>
                        <div className="text-center">Condition:</div>
                        <Row>
                            <Col lg={4} md={6} xs={12}>
                                <FormControlLabel
                                    control={
                                    <Checkbox
                                        checked={settings.condition.indexOf("new") !== -1}
                                        onChange={this.conditionChecked}
                                        value="new"
                                    />
                                    }
                                    label="New"
                                />
                                <FormControlLabel
                                    control={
                                    <Checkbox
                                        checked={settings.condition.indexOf("used_like_new") !== -1}
                                        onChange={this.conditionChecked}
                                        value="used_like_new"
                                    />
                                    }
                                    label="Used - Like New"
                                />
                            </Col>
                            <Col lg={4} md={6} xs={12}>
                                <FormControlLabel
                                    control={
                                    <Checkbox
                                        checked={settings.condition.indexOf("used_very_good") !== -1}
                                        onChange={this.conditionChecked}
                                        value="used_very_good"
                                    />
                                    }
                                    label="Used - Very Good"
                                />
                                <FormControlLabel
                                    className="form-control-label"
                                    control={
                                    <Checkbox
                                        checked={settings.condition.indexOf("used_good") !== -1}
                                        onChange={this.conditionChecked}
                                        value="used_good"
                                    />
                                    }
                                    label="Used - Good"
                                />
                            </Col>
                            <Col lg={4} md={6} xs={12}>
                                <FormControlLabel
                                    className="form-control-label"
                                    control={
                                    <Checkbox
                                        checked={settings.condition.indexOf("used_acceptable") !== -1}
                                        onChange={this.conditionChecked}
                                        value="used_acceptable"
                                    />
                                    }
                                    label="Used - Acceptable"
                                />
                            </Col>
                        </Row>
                    </Col>
                    <Col xs={4} lg={3}>
                        <FormControlLabel
                            control={
                                <ChipInput
                                    // style={{maxHeight: "90px", overflow: "auto"}}
                                    value={this.props.settings.description}
                                    onAdd={chip => {
                                        const chips = this.props.settings.description;
                                        chips.push(chip);
                                        this.props.set_description(chips);
                                    }}
                                    onDelete={(chip, index) => {
                                        const chips = this.props.settings.description;
                                        chips.splice(chips.indexOf(chip), 1);
                                        this.props.set_description(chips);
                                    }}
                            />
                            }
                            label="Description (Does NOT Contain)"
                        />
                        <FormControlLabel
                            control={
                                <ChipInput
                                    // style={{maxHeight: "90px", overflow: "auto"}}
                                    value={this.props.settings.seller_name}
                                    onAdd={chip => {
                                        const chips = this.props.settings.seller_name;
                                        chips.push(chip);
                                        this.props.set_seller_name(chips);
                                    }}
                                    onDelete={(chip, index) => {
                                        const chips = this.props.settings.seller_name;
                                        chips.splice(chips.indexOf(chip), 1);
                                        this.props.set_seller_name(chips);
                                    }}
                            />
                            }
                            label="Seller name (Is NOT)"
                        />
                        <FormControlLabel
                            control={
                                <Checkbox
                                    checked={settings.ships_from_us_only}
                                    onChange={this.shippingChecked}
                                />
                            }
                            label="Shipping Location - Ships from US only (if known)"
                        />
                    </Col>
                </Row>
                <Row>
                    <Col xs={12} className="text-center">
                        <hr />
                        <h5>Alert Notifications</h5>
                    </Col>
                </Row>
                <Row>
                    <Col xs={4} />
                    <Col xs={4} className="text-center">
                        <TextField
                            id={actions.SETTINGS_EMAIL}
                            value={this.props.settings.email}
                            label="Type your email..."
                            margin="normal"
                            variant="standard"
                            type="email"
                            onChange={this.inputChanged}
                            fullWidth
                        />
                    </Col>
                    <Col xs={4} />
                </Row>
                <Row>
                    <Col xs={12} className="text-center">
                        <Button color="primary" variant="contained" onClick={this.saveSettings}>Save Global Settings</Button>
                    </Col>
                </Row>
            </Container>
        )
    }
}

const mapStateToProps = state => {
    return {
        settings: state.settings
    }
}

const mapDispatchToProps = dispatch => {
    return {
        set_isbn: payload => dispatch({type: actions.SETTINGS_ISBN, payload}),
        set_price: payload => dispatch({type: actions.SETTINGS_PRICE, payload}),
        set_conditions: payload => dispatch({type: actions.SETTINGS_CONDITION, payload}),
        set_description: payload => dispatch({type: actions.SETTINGS_DESCRIPTION, payload}),
        set_seller_name: payload => dispatch({type: actions.SETTINGS_SELLER_NAME, payload}),
        set_seller_rating: payload => dispatch({type: actions.SETTINGS_SELLER_RATING, payload}),
        set_seller_rating_percent: payload => dispatch({type: actions.SETTINGS_SELLER_RATING_PERCENT, payload}),
        set_shipping: payload => dispatch({type: actions.SETTINGS_SHIP_US, payload}),
        set_email: payload => dispatch({type: actions.SETTINGS_EMAIL, payload})
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Settings);