const actions = {
    OFFERS_HISTORY_USED_RANGE: "OFFERS_HISTORY_USED_RANGE",
    OFFERS_HISTORY_NEW_RANGE: "OFFERS_HISTORY_NEW_RANGE",
    OFFERS_HISTORY_VISIBLE: "OFFERS_HISTORY_VISIBLE",
}

const initialState = {
    used_offers: [],
    new_offers: [],
    offers_history_visible: false,
}


const offersHistoryReducer = (state=initialState, action) => {
    switch(action.type) {
        case actions.OFFERS_HISTORY_USED_RANGE:
            return {
                ...state,
                used_offers: action.payload
            }

        case actions.OFFERS_HISTORY_NEW_RANGE:
            return {
                ...state,
                new_offers: action.payload
            }
        case actions.OFFERS_HISTORY_VISIBLE:
            return {
                ...state,
                offers_history_visible: !state.offers_history_visible
            }
        default:
            return state;
    }
}


export default offersHistoryReducer;
export {
    actions
}