const actions = {
    PRICE_HISTORY_AMAZON_VISIBLE: "PRICE_HISTORY_AMAZON_VISIBLE",
    PRICE_HISTORY_NEW_VISIBLE: "PRICE_HISTORY_NEW_VISIBLE",
    PRICE_HISTORY_USED_ALL_VISIBLE: "PRICE_HISTORY_USED_ALL_VISIBLE",
    PRICE_HISTORY_USED_LIKE_NEW_VISIBLE: "PRICE_HISTORY_USED_LIKE_NEW_VISIBLE",
    PRICE_HISTORY_USED_VERY_GOOD_VISIBLE: "PRICE_HISTORY_USED_VERY_GOOD_VISIBLE",
    PRICE_HISTORY_USED_GOOD_VISIBLE: "PRICE_HISTORY_USED_GOOD_VISIBLE",
    PRICE_HISTORY_USED_ACCEPTABLE_VISIBLE: "PRICE_HISTORY_USED_ACCEPTABLE_VISIBLE",
    PRICE_HISTORY_SALES_RANK_VISIBLE: "PRICE_HISTORY_SALES_RANK_VISIBLE",
    PRICE_HISTORY_AMAZON_RANGE: "PRICE_HISTORY_AMAZON_RANGE",
    PRICE_HISTORY_NEW_RANGE: "PRICE_HISTORY_NEW_RANGE",
    PRICE_HISTORY_USED_LIKE_NEW_RANGE: "PRICE_HISTORY_USED_LIKE_NEW_RANGE",
    PRICE_HISTORY_USED_VERY_GOOD_RANGE: "PRICE_HISTORY_USED_VERY_GOOD_RANGE",
    PRICE_HISTORY_USED_GOOD_RANGE: "PRICE_HISTORY_USED_GOOD_RANGE",
    PRICE_HISTORY_USED_ACCEPTABLE_RANGE: "PRICE_HISTORY_USED_ACCEPTABLE_RANGE",
    PRICE_HISTORY_SALES_RANK_RANGE: "PRICE_HISTORY_SALES_RANK_RANGE",
    PRICE_HISTORY_RANGE_DAY: "PRICE_HISTORY_RANGE_DAY",
    PRICE_HISTORY_RANGE_WEEK: "PRICE_HISTORY_RANGE_WEEK",
    PRICE_HISTORY_RANGE_MONTH: "PRICE_HISTORY_RANGE_MONTH",
    PRICE_HISTORY_RANGE_3_MONTH: "PRICE_HISTORY_RANGE_3_MONTH",
    PRICE_HISTORY_RANGE_YEAR: "PRICE_HISTORY_RANGE_YEAR"
}

let initialState = {
    amazon_visible: true,
    new_visible: true,
    used_all_visible: true,
    used_like_new_visible: true,
    used_very_good_visible: true,
    used_good_visible: true,
    used_acceptable_visible: true,
    salesRank_visible: false,
    amazon: [],
    newone: [],
    used_like_new: [],
    used_very_good: [],
    used_good: [],
    used_acceptable: [],
    salesRank: [],
    range: actions.PRICE_HISTORY_RANGE_DAY
}

const priceHistoryReducer = (state=initialState, action) => {
    switch(action.type) {
        case actions.PRICE_HISTORY_AMAZON_VISIBLE:
            return {
                ...state,
                amazon_visible: !state.amazon_visible,
            }
        case actions.PRICE_HISTORY_NEW_VISIBLE:
            return {
                ...state,
                new_visible: !state.new_visible,
            }
        case actions.PRICE_HISTORY_USED_ALL_VISIBLE:
            return {
                ...state,
                used_all_visible: !state.used_all_visible,
                used_like_new_visible: !state.used_all_visible,
                used_very_good_visible: !state.used_all_visible,
                used_good_visible: !state.used_all_visible,
                used_acceptable_visible: !state.used_all_visible,
            }
        case actions.PRICE_HISTORY_USED_LIKE_NEW_VISIBLE:
            return {
                ...state,
                used_like_new_visible: !state.used_like_new_visible,
                used_all_visible: !state.used_like_new_visible && state.used_very_good_visible && state.used_good_visible && state.used_acceptable_visible,
            }
        case actions.PRICE_HISTORY_USED_VERY_GOOD_VISIBLE:
            return {
                ...state,
                used_very_good_visible: !state.used_very_good_visible,
                used_all_visible: state.used_like_new_visible && !state.used_very_good_visible && state.used_good_visible && state.used_acceptable_visible,
            }
        case actions.PRICE_HISTORY_USED_GOOD_VISIBLE:
            return {
                ...state,
                used_good_visible: !state.used_good_visible,
                used_all_visible: state.used_like_new_visible && state.used_very_good_visible && !state.used_good_visible && state.used_acceptable_visible,
            }
        case actions.PRICE_HISTORY_USED_ACCEPTABLE_VISIBLE:
            return {
                ...state,
                used_acceptable_visible: !state.used_acceptable_visible,
                used_all_visible: state.used_like_new_visible && state.used_very_good_visible && state.used_good_visible && !state.used_acceptable_visible,
            }
        case actions.PRICE_HISTORY_SALES_RANK_VISIBLE:
            return {
                ...state,
                salesRank_visible: !state.salesRank_visible,
            }
        case actions.PRICE_HISTORY_AMAZON_RANGE:
            return {
                ...state,
                amazon: action.payload,
            }
        case actions.PRICE_HISTORY_NEW_RANGE:
            return {
                ...state,
                newone: action.payload
            }
        case actions.PRICE_HISTORY_USED_LIKE_NEW_RANGE:
            return {
                ...state,
                used_like_new: action.payload
            }
        case actions.PRICE_HISTORY_USED_VERY_GOOD_RANGE:
            return {
                ...state,
                used_very_good: action.payload
            }
        case actions.PRICE_HISTORY_USED_GOOD_RANGE:
            return {
                ...state,
                used_good: action.payload
            }
        case actions.PRICE_HISTORY_USED_ACCEPTABLE_RANGE:
            return {
                ...state,
                used_acceptable: action.payload
            }
        case actions.PRICE_HISTORY_SALES_RANK_RANGE:
            return {
                ...state,
                salesRank: action.payload
            }
        case actions.PRICE_HISTORY_RANGE_DAY:
        case actions.PRICE_HISTORY_RANGE_WEEK:
        case actions.PRICE_HISTORY_RANGE_MONTH:
        case actions.PRICE_HISTORY_RANGE_3_MONTH:
        case actions.PRICE_HISTORY_RANGE_YEAR:
            return {
                ...state,
                range: action.type
            }
        default:
            return state;
    }
}

export default priceHistoryReducer;
export {
    actions
}